import React, { Component } from "react";
import MusicPlayer from "react-jinke-music-player";
import _ from "lodash";
import YTSearch from "youtube-api-search";
import HotKey from "react-shortcut";

import IconMenu from 'material-ui/IconMenu';
import MenuItem from 'material-ui/MenuItem';
import IconButton from 'material-ui/IconButton';
import MoreVertIcon from 'material-ui/svg-icons/navigation/more-vert';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';

import "react-jinke-music-player/assets/index.css";
import logo from "./logo.svg";
import "./App.css";
import rainy from "./tracks/rainy.m4a";
import rainyImg from "./tracks/rainy.jpg";
import whitenoise from "./tracks/whitenoise.m4a";
import whitenoiseImg from "./tracks/whitenoise.jpg";

import Search_bar from "./components/Search_bar";
import Video_list from "./components/Video_list";
import Video_detail from "./components/Video_detail";

const API_KEY = "AIzaSyBYf1d1OI9RrbBZ8ox-HppCUqyndH8herc";

const options = {
  audioLists: [
    {
      singer: "Earth Nature",
      name: "Rainy Mood",
      musicSrc: rainy,
      cover: rainyImg,
    }
  ],

  defaultPlayMode:"singleLoop",

  defaultVolume:0,

  defaultPosition:{
        top:200,
        left:100
    },

  showDowload: false,

  showPlayMode: false,

  showThemeSwitch:false,
}

const options2 = {
  audioLists: [
    {
      singer: 'Celestial Ambient',
      name: 'White Noise',
      musicSrc: whitenoise,
      cover: whitenoiseImg,
    }
  ],

  defaultPlayMode:"singleLoop",

  defaultVolume:0,

  defaultPosition:{
        top:300,
        left:100
    },

  showDowload: false,

  showPlayMode: false,

  showThemeSwitch:false,
}

class App extends Component {
  state = {
    videos: [],
    selectedVideo: null
  };

  componentDidMount() {
    this.videoSearch('synthwave chillwave');
  }

  videoSearch = searchTerm => {
    //youtube search
    YTSearch({key: API_KEY, term:searchTerm}, (videos) => {
      this.setState({
        videos:videos,
        selectedVideo: videos[0],
        menuHeaderClass: "App-header"
       });
    });
  }

  changeMenuState = () => {
    if (this.state.menuHeaderClass === "App-header") {
      this.setState({menuHeaderClass: "App-header-Hidden" })
    } else {
      this.setState({menuHeaderClass: "App-header" })
    }
  }

  menuSearchBar = (event, menuItem, index) => {
    if(menuItem.props.primaryText === "Search Bar") {
      this.changeMenuState()
    }
  }

  render() {
    const videoSearch = _.debounce( (term) => {this.videoSearch(term)},400)

    const descVideo = this.state.selectedVideo ? (<div className="details">
              <MuiThemeProvider>
                <IconMenu className="iconMenu"
                  iconButtonElement={<IconButton><MoreVertIcon color="white" /></IconButton>}
                  anchorOrigin={{horizontal: 'right', vertical: 'top'}}
                  targetOrigin={{horizontal: 'right', vertical: 'top'}}
                  onItemClick={this.menuSearchBar}
                  >
                  <MenuItem primaryText="Search Bar"/>
                </IconMenu>
              </MuiThemeProvider>
              <div>{this.state.selectedVideo.snippet.title}</div>
              <div>{this.state.selectedVideo.snippet.description}</div>
            </div>) : (<span></span>);

    return (
      <div className="App">
        <HotKey
          keys={['#']}
          onKeysCoincide={this.changeMenuState}
        />

        <HotKey
          keys={['`']}
          onKeysCoincide={this.changeMenuState}
        />

        {descVideo}

        <div className={"layerOverVideo"}></div>
        <Video_detail video={this.state.selectedVideo} videos={this.state.videos} />
        <header className={this.state.menuHeaderClass}>
          <Search_bar onSearchTermChange={videoSearch} />
          <Video_list
            onVideoSelect={selectedVideo => this.setState({ selectedVideo })}
            videos={this.state.videos}
          />
        </header>
        <MusicPlayer {...options} />
        <MusicPlayer {...options2} />
        {/* <p className="App-intro">
          To get started, edit <code>src/App.js</code> and save to reload.
        </p> */}
        <div>
        </div>
      </div>
    );
  }
}

export default App;



