import React from "react"

const Video_detail = ( { video, videos }) =>{

  //check before we render video into Video_detail
  if(!video){
    return (<div>Loading...</div>)
  }

  let playlist = ""
  for (let value of videos) {
    if(videos.indexOf(video) != videos.indexOf(value)) playlist += value.id.videoId + ","
  }

  const videoId = video.id.videoId;
  const url = `https://www.youtube.com/embed/${videoId}?autoplay=1&loop=1&playlist=${playlist}`;

  return(
    <div className="video-detail col-md-8">
      <div className="embed-responsive embed-responsive-16by9">
         <iframe className = "embed-responsive-item" src={url} frameBorder="0" ></iframe>
      </div>
    </div>
  )
}

export default Video_detail;



